.item button {
    font-family: 'Montserrat';
    font-weight: 700;
    border: none;
    background-color: #000;
    color: #fff;
    padding: 1em;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 0.5em 1em;
    width: fit-content;
}

.item button:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
    cursor: pointer;
}

.action {
    display: inline;
    margin-left: -1.8em;
}

h6 {
    font-size: 14px;
    margin: 1em 0;
}

p {
    margin-top: 2em;
}

.buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.modal button {
    font-family: 'Montserrat';
    font-weight: 700;
    border: none;
    background-color: #000;
    color: #fff;
    padding: 1em;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 1em;
}
.modal button:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
    cursor: pointer;
}

.modal {
    width: fit-content;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    background-color: #fff;
    padding: 2em 4em;
    font-family: 'Montserrat';
    position: fixed;
    left: 37vw
}

.modal p {
    font-size: 24px;
    font-weight: 700;
    margin-top: 1em;
}